.heart {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.heart-container {
	width: 460px;
	height: 407px;
	position: relative;
}

.heart-container__chat-image {
	width: 335px;
	height: 313px;
	position: absolute;
	inset: 50%;
	transform: translate(-50%, -50%);
	opacity: 60%;
	z-index: 6;
}

@keyframes heart {
	0% {
		transform: translate(-50%, -50%) scale(0.8);
	}
	100% {
		transform: translate(-50%, -50%) scale(1.1);
	}
}

.heart-container__heart-image {
	width: 197px;
	height: 175px;
	position: absolute;
	inset: 50%;
	transform: translate(-50%, -50%);
	z-index: 7;
	animation-name: heart;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate-reverse;
	animation-fill-mode: both;
}

@keyframes first-ellipse {
	0% {
		transform: translate(-5%, 0);
	}
	100% {
		transform: translate(14%, 5%);
	}
}

.heart-container__ellipse-image {
	fill-opacity: 50%;
}

.heart-container__ellipse-image--first {
	position: absolute;
	top: 0;
	left: 5%;
	transform: translate(-5%, 0%);
	z-index: 4;
	animation-name: first-ellipse;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: both;
}

@keyframes second-ellipse {
	0% {
	transform: translate(-85%, -5%);
	}

	100% {
		transform: translate(-100%, 20%);
	}
}

.heart-container__ellipse-image--second {
	position: absolute;
	top: 5%;
	left: 85%;
	transform: translate(-85%, -5%);
	z-index: 4;
	animation-name: second-ellipse;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: both;
}

@keyframes third-ellipse {
	0% {
		transform: translate(0%, -58%);
	}
	100% {
		transform: translate(22%, -58%);
	}
}

.heart-container__ellipse-image--third {
	position: absolute;
	top: 50%;
	left: 0%;
	transform: translate(0%, -50%);
	z-index: 5;
	animation-name: third-ellipse;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: both;
}

@keyframes fourth-ellipse {
	0% {
		transform: translate(-30%, -90%);
	}
	100% {
		transform: translate(-25%, -100%);
	}
}

.heart-container__ellipse-image--fourth {
	position: absolute;
	top: 90%;
	left: 30%;
	transform: translate(-30%, -90%);
	z-index: 5;
	animation-name: fourth-ellipse;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: both;
}

@keyframes fifth-ellipse {
	0% {
		transform: translate(-100%, -100%);
	}
	100% {
		transform: translate(-105%, -105%);
	}
}

.heart-container__ellipse-image--fifth {
	position: absolute;
	top: 100%;
	left: 100%;
	transform: translate(-100%, -100%);
	z-index: 4;
	animation-name: fifth-ellipse;
	animation-duration: 1200ms;
	animation-timing-function: linear;
	animation-delay: 0ms;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: both;
}
